import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./hotcss/hotcss.js";
import 'element-ui/lib/theme-chalk/index.css'
import ElementUI from 'element-ui'
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css';
import './assets/common.css'
import Vant from 'vant';
import 'vant/lib/index.css';
import '../public/config.js'
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate';
import common from '../commonFunc/commonFunc.js'
import 'amfe-flexible/index.js'
Vue.prototype.common = common
extend('required', {
  validate(value) {
    return {
      required: true,
      valid: ['', null, undefined].indexOf(value) === -1
    };
  },
  computesRequired: true
});




import preview from 'vue-photo-preview'
import 'vue-photo-preview/dist/skin.css'
let options = {
  fullscreenEl: false,
  preloaderEl:true,
  closeEl: false, //控制是否显示右上角关闭按钮
  tapToClose: true,
  zoomEl: true, //控制是否显示放大缩小按钮
  tapToToggleControls: false, //点击应切换控件的可见性
  arrowEl: true,  //控制如图的左右箭头（pc浏览器模拟手机时）
  clickToCloseNonZoomable: false, //点击图片应关闭图库，仅当图像小于视口的大小
  history:true
};
Vue.use(preview, options)
Vue.use(preview)



//main.js
import Viewer from 'v-viewer'
import 'viewerjs/dist/viewer.css'
Vue.directive('title', {
  inserted: function (el) {
    document.title = el.dataset.title
  }
})
Vue.use(Viewer);
Viewer.setDefaults({
  'inline':false,
  'button':true, //右上角按钮
  "navbar": false, //底部缩略图
  "title": false, //当前图片标题
  "toolbar": false, //底部工具栏
  "tooltip": false, //显示缩放百分比
  "movable": true, //是否可以移动
  "zoomable": true, //是否可以缩放
  "rotatable": true, //是否可旋转
  "scalable": false, //是否可翻转
  "transition": false, //使用 CSS3 过度
  "fullscreen": true, //播放时是否全屏
  "keyboard": false, //是否支持键盘
  "url": "data-source",
  ready: function (e) {
    console.log(e.type,'组件以初始化');
  },
  show: function (e) {
    console.log(e.type,'图片显示开始');
  },
  shown: function (e) {
    console.log(e.type,'图片显示结束');
  },
  hide: function (e) {
    console.log(e.type,'图片隐藏完成');
  },
  hidden: function (e) {
    console.log(e.type,'图片隐藏结束');
  },
  view: function (e) {
    console.log(e.type,'视图开始');
  },
  viewed: function (e) {
    console.log(e.type,'视图结束');
   },
});

Vue.use(Vant);
Vue.component('ValidationProvider', ValidationProvider);
Vue.component('ValidationObserver', ValidationObserver);
Vue.component('extend', extend);
Vue.config.productionTip = false;
Vue.use(Mint);
Vue.use(ElementUI)



new Vue({
  router,
  store,
  data: function () {
    return {
      global: {
        baseURL: window.global_config.BASE_URL,
        isDoLogin:window.global_config.isDoLogin
      },
      url: {
        homeLogin: ""
      },
      style_cp_img: {
        id: "",
        img_base64_list: [],
        have: false
      },
      style_upload_img: {
        info: {},
        state: 1//1：无图片 2有图片
      },
      style: {
        state: "",//0 ：编辑  1：添加
        num: "",
        num_have: false,//
        ks_have: false,
        ps_have: false,
        cp_have: false
      },
    }
  },
  methods: {
    clear_style_cp_img() {
      this.style_cp_img = {
        id: "",
        img_base64_list: [],
        have: false
      }
    }
  },
  render: h => h(App)
}).$mount("#app");
