
import { Toast } from "mint-ui";
import router from "../src/router";
export default {
  getSettle: function (settle) {
    if (settle == 1) {
      return "月结";
    } else if (settle == 2) {
      return "现金";
    } else if (settle == 3) {
      return "记账";
    }
  },
  getLevel: function (level) {
    if (level == 1) {
      return "零散客户";
    } else if (level == 2) {
      return "普通客户";
    } else if (level == 3) {
      return "重要客户";
    }
  },
  getRequires: function (requires) {
    if (requires == 1) {
      return "严格";
    } else if (requires == 2) {
      return "普通";
    }
  },
  //裁片位置
  getPlaces: function (places) {
    if (places == 1) {
      return "按点位";
    } else if (places == 2) {
      return "按纸样";
    } else if (places == 3) {
      return "按样衣";
    } else if (places == 4) {
      return "按效果图";
    }
  },
  //裁片工艺
  getCraft: function (craft) {
    if (craft == 1) {
      return "丝印";
    } else if (craft == 2) {
      return "数码";
    } else if (craft == 3) {
      return "激光";
    } else if (craft == 4) {
      return "移模";
    }
  },
  //裁片有无拼缝
  getPiece: function (piece) {
    if (piece == 1) {
      return "有拼缝";
    } else if (piece == 2) {
      return "无拼缝";
    }
  },
  //面料来源
  getlinlingFroms: function (linlingFrom) {
    if (linlingFrom == 1) {
      return "自家";
    } else if (linlingFrom == 2) {
      return "客源";
    }
  },
  //数组删除
  indexVf(arr, item) {
    for (var i = 0; i < arr.length; i++) {
      if (arr[i] == item) {
        return i;
      }
    }
  },
  format(fmt, str) {
    if (str == "") {
      return "";
    } else {
      var o = {
        "M+": str.getMonth() + 1, //月份
        "d+": str.getDate(), //日
        "h+": str.getHours(), //小时
        "m+": str.getMinutes(), //分
        "s+": str.getSeconds(), //秒
        "q+": Math.floor((str.getMonth() + 3) / 3), //季度
        S: str.getMilliseconds() //毫秒
      };

      if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          (str.getFullYear() + "").substr(4 - RegExp.$1.length)
        );
      }

      for (var k in o) {
        if (new RegExp("(" + k + ")").test(fmt)) {
          fmt = fmt.replace(
            RegExp.$1,
            RegExp.$1.length == 1
              ? o[k]
              : ("00" + o[k]).substr(("" + o[k]).length)
          );
        }
      }

      return fmt;
    }
  },
  getErr(error) {
    switch (error) {
      case "":
        return "无异常";
      case 1:
        return "补片";
      case 2:
        return "返工";
      case 3:
        return "重做";
    }
  },
  getProgress(progress) {
    switch (progress) {
      case 0:
        return "未下单";
      case 1:
        return "开发";
      case 2:
        return "首样";
      case 3:
        return "改版";
      case 4:
        return "翻样";
      case 5:
        return "产前样";
      case 6:
        return "首单";
      case 7:
        return "翻单";
      case 8:
        return "补片(打样)";
      case 9:
        return "补片(大货)";
      case 10:
        return "返工(打样)";
      case 11:
        return "返工(大货)";
      case 12:
        return "重做(打样)";
      case 13:
        return "重做(大货)";
      case 50:
        return "裁片未到";
      case 51:
        return "裁片已到";
    }
  },
  getTeamType(type) {
    switch (type) {
      case 1:
        return "定位 ";
      case 2:
        return "点数 ";
      case 3:
        return "刮板";
      case 4:
        return "总检（台上）";
      case 5:
        return "总检（台道）";
    }
  },
  errHandle(err, callback) {
    console.log(err)
    if (err.response.code == 500) {
      console.log(err.response)
      Toast({
        message: "服务器异常！",
        position: "middle",
        duration: 1500
      });
    }
    if (err.response.data.state.code == -2) {
      console.log("-2")
      sessionStorage.setItem("token", "");
      Toast({
        message: "登录过期，重新登录！",
        position: "middle",
        duration: 1500
      });
      router.replace("/");
    } else if (err.response.data.state.code == -3) {
      console.log("-3")
      Toast({
        message: "禁用，无法登录！",
        position: "middle",
        duration: 1500
      });
      router.replace("/blank");
    }
    else {
      if (err.response.data.state.msg.length > 50) {
        Toast({
          message: "未知错误！",
          position: "middle",
          duration: 1500
        });
      } else {
        Toast({
          message: err.response.data.state.msg,
          position: "middle",
          duration: 1500
        });
      }
    }
    if (callback) {
      callback();
    }
  },
  fun_date(aa) {
    let date1 = new Date();
    //time1=date1.getFullYear()+"-"+(date1.getMonth()+1)+"-"+date1.getDate();//time1表示当前时间  
    let date2 = new Date(date1);
    date2.setDate(date1.getDate() + aa);
    let time2 = new Date(date2.setDate(date1.getDate() + aa));
    return time2
  }

}
